
* {
    font-family: Arial, sans-serif;
}
:focus {
    outline: none !important;
}
:focus::-webkit-input-placeholder {
    color: transparent
}
:focus::-moz-placeholder {
    color: transparent
}
:focus:-moz-placeholder {
    color: transparent
}
:focus:-ms-input-placeholder {
    color: transparent
}
.navbar-root {
    color: white;
    background-color: #2b2d33;
}
.navbar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-left: 20px;
}
.navbar-brand {
    display: inline-block;
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    line-height: inherit;
    white-space: nowrap;
}
.navbar-nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
ul {
    padding-left: 0;
    padding-right: 0;
    list-style-type: none
}
.nav-item {
    margin-right: 15px;
}
a {
    text-decoration: none;
    color: white;
}
.no-auth {
    text-align:center;
    color: white;
}
.no-auth a,
.no-auth a:visited {
    text-align:center;
    color: rgb(145, 215, 255);
}
a:visited,
a:active,
.nav-link:active,
.nav-link:visited {
    color: white;
}

/* signup */
#form-signup {
    margin-top: 150px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.form-header {
    margin-top: 0;
}
.form-signup__container {
    width: 300px;
    text-align: center;
    background: #fff;
    padding: 13px;
    border-radius: 15px;
}
#form-signup p {
    margin: 0;
}
.input {
    margin-bottom: 10px;
}
.btn {
    background-color: #afc9fa;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    padding: 2px 5px;
    border-radius: 3px;
    cursor: pointer;
}
.btn:hover {
    background-color: #7cb0ff;
}

/* signup */
body {
    background-color: #35383f;
    padding: 0;
    margin: 0;
}
.kanban-board {
    padding: 50px 0 50px 50px;
    width: 1305px;
    margin: 0 auto;
}
.row {
    background-color: #2b2d33;
    width: 272px;
    padding-bottom: 46px;
    display: inline-block;
    vertical-align: top;
    position: relative;
}
.row:not(:last-child) {
    margin-right: 50px;
}
.header {
    color: white;
    text-shadow: 0 0 4px #4b5263;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 200;
    margin: 0;
    padding: 10px;
}
.orange {
    background-color: #fb7e46;
}
.blue {
    background-color: #2a92bf;
}
.yellow {
    background-color: #f4ce46;
}
.green {
    background-color: #00b961;
}
.draggable-item {
    background-color: #191a1d;
    margin: 10px 10px 0;
    padding: 10px;
    cursor: move;
    color: #aaaaaa;
    position: relative;
}
.draggable-item p {
    font-size: 14px;
}
.draggable-item .id {
    color: white;
}
.delete {
    position: absolute;
    top: 3px;
    right: 3px;
    width: 15px;
    height: 15px;
    margin: 0;
    padding: 0;
    line-height: 1;
    cursor: pointer;
    opacity: 0.5;
}
.delete:before,
.delete:after {
    background-color: white;
    position: absolute;
    top: 7px;
    right: 2px;
    content: "";
    display: block;
    width: 11px;
    height: 1px;
}
.delete:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.delete:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.delete:hover {
    opacity: 1;
}
.card-title-textarea {
    resize: none;
    width: 230px;
    border: none;
    margin: 10px;
    padding: 10px;
    color: #cfcfcf;
    background-color: #4f4f4f;
    border: 1px solid #4f4f4f;
}
.card-title-textarea::-webkit-input-placeholder {
    color: #9f9f9f;
}
.card-title-textarea:-moz-placeholder {
    color: #9f9f9f;
}
.card-title-textarea::-moz-placeholder {
    color: #9f9f9f;
}
.card-title-textarea:-ms-input-placeholder {
    color: #9f9f9f;
}
.add-a-card {
    position: absolute;
    bottom: 15px;
    left: 10px;
    padding: 5px;
    width: 170px;
    font-size: 14px;
    color: #cfcfcf;
    background-color: #616161;
    border: 1px solid #616161;
    cursor: pointer;
}
.add-a-card:hover {
    color: #cdcdcd;
    background-color: #504f4f;
    border: 1px solid #504f4f;
}
.add-a-card:active {
    color: #cacaca;
    background-color: #454545;
    border: 1px solid #454545;
}
.cancel {
    position: relative;
    position: absolute;
    bottom: 15px;
    left: 190px;
    background-color: transparent;
    border: none;
    width: 28px;
    height: 28px;
    cursor: pointer;
    opacity: 0.5;
}
.cancel:before,
.cancel:after {
    background-color: white;
    position: absolute;
    top: 13px;
    left: 5px;
    content: "";
    display: block;
    width: 18px;
    height: 2px;
}
.cancel:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.cancel:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.cancel:hover {
    opacity: 1;
}
.add-another-card {
    background-color: transparent;
    border: none;
    width: 100%;
    padding: 10px 36px;
    position: absolute;
    bottom: 0;
    text-align: left;
    color: #757577;
    font-size: 14px;
    cursor: pointer;
}
.add-another-card:before,
.add-another-card:after {
    background-color: #757577;
    position: absolute;
    bottom: 16px;
    left: 10px;
    content: "";
    display: block;
    width: 14px;
    height: 2px;
}
.add-another-card:before {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}
.add-another-card:hover {
    color: #878792;
    background-color: #222328;
}
